    content: `All Missouri LLC business members or managers who take profits out of the LLC will need to pay self-employment tax. This tax is also known as FICA, Social Security or Medicare tax. It applies to all the earnings you withdraw from your Missouri business. The current self-employment tax rate is 15.3 percent.`,
